.serve-box {
    width: 100vw;
    padding: 10px;
    
}

.service-box {
        width: 90%;
        height: 100%;
        margin: auto;
       align-items: center;
       justify-content : center;
        display: flex;
        flex-wrap: wrap;
        overflow-x: hidden;
}

.card-service {
 
    margin: 25px;
    height: 300px;
    width: 270px;
    border-top: 1.5px solid #e0d9d9;
    border-bottom: 1.5px solid #e0d9d9;
    -webkit-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    -moz-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 35px;
    border-radius: 25px ;
    -webkit-border-radius: 25px ;
    -moz-border-radius: 25px ;
    -ms-border-radius: 25px ;
    -o-border-radius: 25px ;
    transition: 0.4s ease;
    -webkit-transition: 0.4s ease;
    -moz-transition: 0.4s ease;
    -ms-transition: 0.4s ease;
    -o-transition: 0.4s ease;
    cursor: pointer;
    animation: updown 4s infinite;
    -webkit-animation: updown 4s infinite;
}
@keyframes updown {
    0%{
        transform: translateY(0);
}
    25%{
        transform: translateY(-4px);
     
}
    50%{
        transform: translateY(4px);
      
}   
    100%{
        transform: translateY(0);
        
}
}

.card-service:hover {
    transform: translateY(-3px);
    -webkit-transform: translateY(-3px);
    -moz-transform: translateY(-3px);
    -ms-transform: translateY(-3px);
    -o-transform: translateY(-3px);
}
.card-service:hover .ser-img img {
   transform:scale(0.85);
   -moz-transform:scale(0.85);
   -ms-transform:scale(0.85);
   -o-transform:scale(0.85);
   -webkit-transform:scale(0.85);
}


   .ser-img{
    margin-top: -55px;
    width: 80px;
    height: 80px;
    overflow: hidden;
    border-radius: 50%;
    background-color: white;
    /* border: 1.5px solid #e0d9d9; */
}
.ser-img img {
    width: 100%;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
}

.ser-mat {
    width: 100%;
    text-align: center;
    margin : auto;
    justify-content: space-around;

}
.ser-mat h3 {
    padding: 10px;
    font-size : 1.35em
}

.service-btn{
    font-size: 0.85em;
    padding: 8px;
    display: block;
    width: 90%;
    margin: auto;
    border: none;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    outline: none;
    background-color: rgb(0, 149, 255);
    color: white;
    transition: 1s ease ;
    -webkit-transition: 1s ;
    -moz-transition: 1s ;
    -ms-transition: 1s ;
    -o-transition: 1s ;
    font-family: Calibri;
  
}

.service-btn:hover {
    color: white;
    transform: translateY(4px);
    -webkit-transform: translateY(4px);
    -moz-transform: translateY(4px);
    -ms-transform: translateY(4px);
    -o-transform: translateY(4px);
    -webkit-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    -moz-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    text-shadow: 2px 4px 18px rgb(255, 255, 255);
}