* {
    box-sizing: border-box;
}

.title-word {
    animation: color-animation 4s linear infinite;
  }
  
  .title-word-1 {
    --color-1: rgb(140, 0, 255);
    --color-2: #d900ff;
    --color-3: #5d00ff;
  }
  
  .title-word-2 {
    --color-1: #d900ff;
    --color-2: #5d00ff;
    --color-3: rgb(140, 0, 255);
  }
  
  .title-word-3 {
    --color-1: #5d00ff;
    --color-2: rgb(140, 0, 255);
    --color-3: #d900ff;
  }
  
 
  
  @keyframes color-animation {
    0%    {color: var(--color-1)}
    32%   {color: var(--color-1)}
    33%   {color: var(--color-2)}
    65%   {color: var(--color-2)}
    66%   {color: var(--color-3)}
    99%   {color: var(--color-3)}
    100%  {color: var(--color-1)}
  }
  
  /* Here are just some visual styles. 🖌 */
  
  
  .heading-main {
    font-size: 2.5em;
    font-family: 'Oswald', sans-serif;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-weight : 500;
  }

  .p-box {
    height: fit-content;
  }
  .p-box p {
    font-size: 1rem ;
    width: 100%;
    margin: auto;
    text-align: justify;
}
.about-sec {
    width: 100vw;
    background-color: rgba(242, 237, 237, 0.294);
}
.card-box {
     width: 90%;
     overflow-x: hidden;
    margin-top: 20px; 
    margin: auto;  
    
}
  
.abt-card {
    background-color: white;
    padding: 18px;
    border-radius: 15px;
    margin-top: 30px;
    width: 100%;
    -webkit-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    -moz-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.24);
    transition: 0.5s;
    display: flex;
    flex-direction: row;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.abt-card:hover{
    -webkit-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.31);
    -moz-box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.31);
    box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.31);
    transform:  translateY(3px);
    -webkit-transform:  translateY(3px);
    -moz-transform:  translateY(3px);
    -ms-transform:  translateY(3px);
    -o-transform:  translateY(3px);
}


.img-abt {
    flex-basis: 30%;
    overflow: hidden;

}
.card-img {
    width: 100%;
    margin : auto;
}
.card-mat {
   flex-basis: 70%;

}
.card-mat h3 {
    padding: 10px;
    font-size: 1.8em;
}
.card-mat p {
    font-size : 1em;
    padding : 10px;
    padding-top: 0;
}

#reverse-card {
    flex-flow: row-reverse;
}

@media (max-width: 1000px) {
    .card-mat h3 {
        font-size: 1.6em;
        font-weight: 500;
        padding: 5px;
    }
    .card-mat p {
        font-size: 0.8em;
    }
}

@media (max-width : 735px) {
    .abt-card {
        flex-direction: column;
        align-items: center;
    }
    .img-abt{
        width: 40%;
        align-items: center;
        display: flex;
    }
    #reverse-card {
        flex-flow: column;
    }
    .card-mat {
        text-align: center;
    }
   
}

.developer-card {
    display: flex;
    justify-content: space-around;
}
.dev-card {
    flex-basis: 80%;
}
@media (max-width: 500px) {
    .developer-card {
        flex-direction: column;
    }
}