
.review-sec {
    width: 100vw;
    height: auto;
    margin: auto;
    margin-top: 35px;
    text-align: center;
}

.review-box {
    /* background-color: red; */
    width: 100%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.review {
    width: 500px;
    box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.1);
    background-color: white;
    cursor: pointer;
    margin: 10px;
    padding: 20px;
}

.review-top {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 20px;
}

.profile-pic {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}

.profile-pic img {
    object-fit: cover;
    object-position: center;
    width : 100%;
    height: 100%;
}

.name-user {
    display: flex;
    flex-direction: column;
}

.name-user h2 {
    color: rgb(83, 77, 77);
    letter-spacing: 0.rem;
    font-size: 1.2rem;
    margin: 0;
}
.name-user span {
    color: grey;
    font-size: 0.7rem;
}

.review-i {
    color: orange;
}

.review-comment {
    /* padding: 10px; */
    font-size: 0.7rem;
    color: rgb(97, 94, 94);
}

.review:hover {
    transform: translateY(-10px);
    -webkit-transform: translateY(-10px);
    -moz-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    -o-transform: translateY(-10px);
    transition: 0.5s ease;
    -webkit-transition: 0.5s ease;
    -moz-transition: 0.5s ease;
    -ms-transition: 0.5s ease;
    -o-transition: 0.5s ease;
}

@media (max-width : 500px) {
    .name-user h2 {
        font-size: 0.9rem;
    }
    .name-user span {
        font-size: 0.5rem;
    }
    .review i {
        font-size: 0.7rem;
    }
}
