.hired-box {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
    margin-top: 40px;
    justify-content: space-around;
    overflow: hidden;
}

.hired-img {
    flex-basis: 40%;
    align-items: center;
    justify-content: center;
    display: flex;
    overflow: hidden;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}
.hired-img img {
    width: 100%;
    animation: 2s ease infinite updown ;
    -webkit-animation: 4s ease infinite updown ;
}
.hired-content {
    flex-basis: 55%;
}




.clip3 {
    -webkit-clip-path: circle(50% at 50% 50%);
    clip-path: circle(50% at 50% 50%);
    background-color: rgba(255, 162, 0, 0.685);
    width: 300px;
    height: 300px;
    margin-top: 10%;
    position: fixed;
    border: none;
    right: -12%;
    z-index: -1;
    animation: bouncy 5s infinite;
    -webkit-animation: bouncy 5s infinite;
  }
  
  @keyframes bouncy {
      0% {
          transform: translateY(0);
          transform: scale(0.7);
          -webkit-transform: scale(0.7);
          -moz-transform: scale(0.7);
          -ms-transform: scale(0.7);
          -o-transform: scale(0.7);
  }
      25% {
          transform: scale(1.4);
          transform: translateY(20px);
          -webkit-transform: scale(1.4);
          -moz-transform: scale(1.4);
          -ms-transform: scale(1.4);
          -o-transform: scale(1.4);
  }
      50% {
          transform: scale(1);
          transform: translateY(-20px);
          -webkit-transform: scale(1);
          -moz-transform: scale(1);
          -ms-transform: scale(1);
          -o-transform: scale(1);
  }
      100% {
          transform: scale(0.7);
          transform: translateY(0);
          -webkit-transform: scale(0.7);
          -moz-transform: scale(0.7);
          -ms-transform: scale(0.7);
          -o-transform: scale(0.7);
  }
  }
  
.clip2 {
    -webkit-clip-path: circle(50% at 50% 50%);
    clip-path: circle(50% at 50% 50%);
    background-color: rgba(0, 225, 255, 0.545);
    width:350px;
    height: 350px;
    position: fixed;
    display: none;
    border: none;
    left: -12%;
    z-index: -1;
    margin-top: 22%;
    animation: bouncy 5s infinite;
    -webkit-animation: bouncy 5s infinite;
  }
 
  
  @media (max-width : 700px) {
    .hired-box {
        flex-direction: column;
        height: auto;
    }
    .hired-img {
        flex-basis: 25%;
    }
    .clip3 {
        margin-top: 21%;
    }
    .clip2 {
        display: block;
    }
    
  }

  @media  (max-width : 600px) {
    .clip2 {
        left: -19%;
    }
    .clip3{
        right: -20%;
    }
  }

  @media  (max-width : 500px) {
    .clip2 {
        left: -35%;
        width: 200px;
        height: 200px;
    }
    .clip3{
        right: -35%;
        width: 200px;
        height: 200px;
    }
  }



  .spinner-box {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
 
 .alert-box {
    text-align: center;
    position: fixed;
    left: 50%;
    top: 20%;
    transform: translate(-50% , -30%);
    -webkit-transform: translate(-50% , -30%);
    -moz-transform: translate(-50% , -30%);
    -ms-transform: translate(-50% , -30%);
    -o-transform: translate(-50% , -30%);
    z-index: 9;
    width: 250px;
    background-color: rgba(255, 255, 255, 0.492);
    overflow: hidden;
    transition: 0.5s ease-in-out;
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -ms-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    cursor: pointer;
}

.sideIcon {
    width: 30px;
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    top: 25%;
    background-color: rgba(255, 255, 255, 0.459);
}
.sideIcon a {
    font-size: 30px;
    text-decoration: none;
    margin: 0;
}
