* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth
}



@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');



/* Nav Bar */


.nav-bar {
  justify-content: space-between;
  color: rgb(195, 0, 255);
  height: 55px;
  /*background-color: rgba(195, 0, 255, 0.66);
  */display: flex;
  top: 0;
  left: 0;
  align-items: center;
  z-index: 5;
  font-family: 'Oswald', sans-serif;
  /*position: fixed;
  */width: 100vw;
  transition: 0.4s ease ;
  -webkit-transition: 0.4s ease ;
  -moz-transition: 0.4s ease ;
  -ms-transition: 0.4s ease ;
  -o-transition: 0.4s ease ;
}

.stick {
  height: 55px;
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 12px 21px -6px rgba(166, 0, 255, 0.25);
  -webkit-box-shadow: 0px 12px 21px -6px rgba(166, 0, 255, 0.25);
  width: 100vw;
  top: 0;
  left: 0;
  align-items: center;
  z-index: 5;
  background-color: rgb(195, 0, 255);
  color: white;
  font-family: 'Oswald', sans-serif;
  position: fixed;
  transition: 0.4s ease ;
  -webkit-transition: 0.4s ease ;
  -moz-transition: 0.4s ease ;
  -ms-transition: 0.4s ease ;
  -o-transition: 0.4s ease ;
}

.nav-logo {
    width: 25%;
    height: 100%;
    margin-left: 15px;
    align-items: center;
    padding:  10px;
}
.nav-logo h1 {
  font-size : 30px;
  letter-spacing: 1px;
}
.nav-ul {
  display: flex;
  width: 70%;
  align-items: center;
  z-index: 9;
  display: fixed;
}
.nav-ul li {
  list-style-type: none;
  padding: 15px 30px;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  color: rgba(195, 0, 255, 0.66);
}

.nav-ul li:hover  {
  transform: translateX(3px);
  -webkit-transform: translateX(3px);
  -moz-transform: translateX(3px);
  -ms-transform: translateX(3px);
  -o-transform: translateX(3px);
  text-shadow: 2px 0px 15px white;
}

.nav-a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 300;
  color: rgb(195, 0, 255) ;
}
.nav-a-scrolled {
  text-decoration: none;
  font-size: 18px;
  font-weight: 300;
  color: white;
}
.nav-a:hover {
  color: rgb(195, 0, 255) ;
}
.nav-a-scrolled:hover {
  color: white;
}

/* drop down  */

.drop-down {
  position: fixed;
  overflow: hidden;
  width: 150%;
  margin-top: 14px;
  display: none;
 
  z-index:  -9;
  background-color:   rgb(195, 0, 255) ;
}

#about:hover .drop-down{
  display: block;
}

.drop-down a {
  width: 100%;
  display: inline-block;
  padding: 15px;
  color: white;
  text-decoration: none;
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
  -moz-transition: 0.3s ease;
  -ms-transition: 0.3s ease;
  -o-transition: 0.3s ease;
}
.drop-down a:hover {
  background-color:  rgb(255, 0, 230) ;
  transform: scale(1.05);
  color: white;
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
  text-shadow: 2px 0px 15px white;
}






/* Drop down 2  */

.drop-down2 {
  position: fixed;
  overflow: hidden;
  width: 150%;
  margin-top: 14px;
  display: none;
  z-index: index -9;;
  background-color:  rgb(195, 0, 255) ;
}

#code:hover .drop-down2{
  display: block;
}

.drop-down2 a {
  width: 100%;
  display: inline-block;
  padding: 15px;
  text-decoration: none;
  color: white;
  transition: 0.5s ease;
  -webkit-transition: 0.5s ease;
  -moz-transition: 0.5s ease;
  -ms-transition: 0.5s ease;
  -o-transition: 0.5s ease;
}
.drop-down2 a:hover {
  background-color:  rgb(255, 0, 230) ;
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
  text-shadow: 2px 0px 15px white;
  color: white;
}

@media (max-width: 735px) {
  .drop-down , .drop-down2 {
    display: none;
  }
  #about:hover .drop-down {
    display: none;
  }
  #code:hover .drop-down2 {
    display: none;
  }
}






