/* Main Container */


.background {
  width: 100vw;
  /* height: 100vh; */
  display: flex;
}

.content {
  flex-basis: 35%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content :center;
}
.content h1 {
  font-size: 2.2em;
  font-family: 'Oswald', sans-serif;
  color:  rgb(255, 0, 230) ;
}
.content p {
  font-size: 1em;
}

.main-cont {
  flex-basis: 60%;
  align-items: center;
  justify-content: center;
  display: flex;
  overflow: hidden;
  border-radius: 50%;
  z-index: 1;
}

.main-cont img {
  width: 90%;
  animation: 2s ease infinite updown ;
  -webkit-animation: 4s ease infinite updown ;
}

@keyframes updown {
  0% {
      transform: translateY(0);
}
  33% {
      transform: translateY(-4px);
      -webkit-transform: translateY(-4px);
      -moz-transform: translateY(-4px);
      -ms-transform: translateY(-4px);
      -o-transform: translateY(-4px);
}
  66% {
      transform: translateY(4px);
      -webkit-transform: translateY(4px);
      -moz-transform: translateY(4px);
      -ms-transform: translateY(4px);
      -o-transform: translateY(4px);
}
  100% {
      transform: translateY(0);
      -webkit-transform: translateY(0);
      -moz-transform: translateY(0);
      -ms-transform: translateY(0);
      -o-transform: translateY(0);
}
  
}

@media (max-width : 1000px) {
  .content h1 {
    font-size: 1.6em;
    font-family: 'Oswald', sans-serif;
    color:  rgb(255, 0, 230) ;
  }
  .content p {
    font-size: 0.7em;
  }
  
}


@media (max-width : 700px) {
  
  
  .background {
      flex-direction: column;
      height: 70vh;
     
  }
  .content {
      width: 85%;
      margin: auto;
      margin-top: 5%;
      flex-basis: 30%;
  }
  .content h1 {
      font-size: 2.2em;
  }
  .content p {
    font-size: 1em;
  }
  .main-cont {
      flex-basis: 70%;
      justify-content: center;
      align-items: flex-start;
  }
  .nav-a, .nav-a-scrolled {
    text-decoration: none;
    font-size: 18px;
    font-weight: 300;
    color: white;
  }

}


@media (max-width : 500px) {
  .background {
      flex-direction: column;
      height: 80vh;
  }
  .content {
      width: 85%;
      margin: auto;
      margin-top: 15%;
      flex-basis: 40%;
  }
  .content h1 {
      font-size: 24px;
  }
  .content p {
      font-size: 13px;
  }
  .main-cont {
      flex-basis: 60%;
      justify-content: center;
      align-items: flex-start;
  }

}


/* Main button/ */

.main-btn {
  position: relative;
  background-color: transparent;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1em;
  padding: 10px 15px;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  border: 1.5px solid white;
  width: 100%;
  animation: 5s glow infinite;
  -webkit-animation: 5s glow infinite;
}
.main-btn:hover {
  letter-spacing: 0.15em;
  color: white;
  text-shadow: 0 0 30px #fff;
  border: 1.5px solid rgba(195, 0, 255, 0.66);
  box-shadow: 0 0 30px rgba(195, 0, 255, 0.66);
}

.main-btn::before {
  content: '';
  position: absolute;
  inset: 2px;
  background-color: transparent;
}
.main-btn span {
  position: relative;
  z-index: 1;
}
.main-btn i {
  position: absolute;
  inset: 0;
  display: block;
}
.main-btn i::before{
  content: '';
  position: absolute;
  border: 1.5px solid white;
  width: 10px;
  height: 5px;
  top: -3px;
  left: 80%;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
.main-btn:hover  i::before {
  width: 20px;
  left: 20%;
}


.main-btn i::after{
  content: '';
  position: absolute;
  border: 1.5px solid white;
  width: 10px;
  height: 5px;
  bottom: -3px;
  left: 20%;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
.main-btn:hover  i::after {
  width: 20px;
  left: 80%;
}

@keyframes glow {
  0% {
    box-shadow: 0 0 30px rgba(195, 0, 255, 0.66) ;
  }
  25% {
    box-shadow: 0 0 30px rgba(132, 0, 255, 0.66) ;
  }
  50% {
    box-shadow: 0 0 30px rgb(191, 0, 255) ;
  }
  100% {
    box-shadow: 0 0 30px rgba(132, 0, 255, 0.66) ;
  }
  
}

/* Anchor  */
.explore-btn {
  text-decoration: none;
  list-style: none;
}
/* Button */
.button-explore:hover  {
  transition: 0.5s ease-in-out;
  transform: translateY(4px);
  -webkit-transform: translateY(4px);
  -moz-transform: translateY(4px);
  -ms-transform: translateY(4px);
  -o-transform: translateY(4px);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -ms-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
}
