.contact-page {
    width: 100vw;
    height: auto;
    background-color: rgba(236, 242, 242, 0.527);
}
.clip1 {
  -webkit-clip-path: circle(50% at 50% 50%);
  clip-path: circle(50% at 50% 50%);
  background-color: rgba(195, 0, 255, 0.66);
  width: 300px;
  height: 300px;
  position: absolute;
  border: none;
  left: -12%;
  z-index: -1;
  margin-top: 20%;
  animation: bouncy 5s infinite;
  -webkit-animation: bouncy 5s infinite;
}

@keyframes bouncy {
    0% {
        transform: translateY(0);
        transform: scale(0.7);
        -webkit-transform: scale(0.7);
        -moz-transform: scale(0.7);
        -ms-transform: scale(0.7);
        -o-transform: scale(0.7);
}
    25% {
        transform: scale(1.4);
        transform: translateY(20px);
        -webkit-transform: scale(1.4);
        -moz-transform: scale(1.4);
        -ms-transform: scale(1.4);
        -o-transform: scale(1.4);
}
    50% {
        transform: scale(1);
        transform: translateY(-20px);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
}
    100% {
        transform: scale(0.7);
        transform: translateY(0);
        -webkit-transform: scale(0.7);
        -moz-transform: scale(0.7);
        -ms-transform: scale(0.7);
        -o-transform: scale(0.7);
}
}


@media (max-width : 700px) {
    .clip1 {
        left: -20%;
    }
}
@media (max-width : 500px) {
    .clip1 {
        left: -30%;
        width: 250px;
        height: 250px;
    }
}

.contactWindow {
    width: 400px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80vh;
    z-index: 9;
    background-color:white;
    border-radius: 17px;
    overflow: scroll;
    
    box-shadow: -1px 21px 67px -38px rgba(0,0,0,0.44);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
}
.contactWindow-close {
   display: none;
}

@media (max-width : 500px) {
    .contactWindow {
        width: 350px;
    }
}
@media (max-width : 380px) {
    .contactWindow {
        width: 290px;
    }
}

.contactWindow::-webkit-scrollbar {
    display: none;
}

.contactWindow h1 {
    margin: auto;
    font-size: 25px;
    margin-top: 15px;
    color: orange;
    font-weight: 500;
}

.upper {
    width: 100%;
    display: flex;
    height: 50px;
    background-color: rgb(255, 255, 255);
    /* position: fixed;
    display: fixed; */
}
.contactWindow i {
    color:orange;
    margin-top: 20px;
    margin-right: 8px;
    cursor: pointer;
}

